.header__link {
  margin: 0;
  border: none;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 2;
  color: #a9a9a9;
  background-color: #000;
  text-decoration: none;
  letter-spacing: 0.75px;
  height: 50%;
}

.header__link:hover {
  cursor: pointer;
  opacity: 0.6;
}