.admittance__input {
  display: block;
  border: 0;
  margin: 0 0 24px 0;
  padding: 0;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 2;
  color: #fff;
  border-bottom: 2px solid #ccc;
  width: 100%;
  background-color: #000;
}

.admittance__input:focus {
  outline: 0;
}

.admittance__input::placeholder {
  color: #C4C4C4;
}