.admittance__button {
  margin: 0;
  padding: 0;
  border: none;
  width: 100%;
  min-height: 50px;
  background-color: #fff;
  border-radius: 2px;
  color: #000;
  font-family: 'Inter', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 2;
}

.admittance__button:hover {
  cursor: pointer;
  opacity: 0.6;
}