.profile {
  padding: 40px 0 50px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media (max-width: 1170px) {
  .profile {
    padding: 42px 0 50px 0;
    flex-direction: column;
  }
}

@media (max-width: 465px) {
  .profile {
    padding: 42px 0 36px 0;
  }
}